import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchEpisodes, saveWatchList } from "../../redux/reducer";
import { InputGroup, FormControl, Button } from "react-bootstrap";

export default function MyWatchList() {
  const [find, setFind] = React.useState("");
  const dispatch = useDispatch();

  React.useEffect(() => {
    dispatch(fetchEpisodes(null, find));
  }, [find, dispatch]);

  const { episodes } = useSelector(({ reducer }) => {
    return {
      episodes: reducer.episodes,
    };
  });

  const add = (find) => {
    saveWatchList(find);
    setFind('')
  };

  return (
    <div className="m-3" style={{ margin: "0 auto" }}>
      <InputGroup className="mb-3" style={{ width: "80%", margin: "0 auto" }}>
        <FormControl
          placeholder=""
          list="exampleList"
          onChange={(e) => setFind(e.target.value)}
          value={find}
        />
        <datalist id="exampleList">
          {episodes &&
            episodes.map((episode) => {
              return <option key={episode.id} value={episode.name} />;
            })}
        </datalist>
        <InputGroup.Append>
          <Button variant="outline-secondary" onClick={()=>add(find)}>
            Add to WatchList
          </Button>
        </InputGroup.Append>
      </InputGroup>
    </div>
  );
}
