import axios from "axios";

const SET_CHARACTERS = "characters/SET_CHARACTERS";
const SET_EPISODES = "episodes/SET_EPISODES";
const SET_LOCATIONS = "locations/SET_LOCATIONS";
const SET_WATCHLIST = "watchList/SET_WATCHLIST";
const SET_REQUESTINFO = "requestInfo/SET_REQUESTINFO";

let initialState = {
  characters: [],
  episodes: [],
  locations: [],
  requestInfo: {},
  watchList: [],
};
export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CHARACTERS:
      return {
        ...state,
        characters: action.payload,
      };
    case SET_EPISODES:
      return {
        ...state,
        episodes: action.payload,
      };
    case SET_LOCATIONS:
      return {
        ...state,
        locations: action.payload,
      };
    case SET_WATCHLIST:
      return {
        ...state,
        watchList: action.payload,
      };
    case SET_REQUESTINFO:
      return {
        ...state,
        requestInfo: action.payload,
      };
    default:
      return state;
  }
};

const setCharacters = (items) => ({
  type: SET_CHARACTERS,
  payload: items,
});
const setRequestInfo = (items) => ({
  type: SET_REQUESTINFO,
  payload: items,
});
const setEpisodes = (items) => ({
  type: SET_EPISODES,
  payload: items,
});
const setLocations = (items) => ({
  type: SET_LOCATIONS,
  payload: items,
});
const setWatchList = (items) => ({
  type: SET_WATCHLIST,
  payload: items,
});

export const fetchCharacters =
  (active, species, status, gender) => (dispatch) => {
    let url = `https://rickandmortyapi.com/api/character/?page=${active}`;
    if (species) url += `&species=${species}`;
    if (status) url += `&status=${status}`;
    if (gender) url += `&gender=${gender}`;
    if (species && status) url += `&species=${species}&status=${status}`;
    if (species && gender) url += `&species=${species}&gender=${gender}`;
    if (status && gender) url += `&status=${status}&gender=${gender}`;
    if (species && status && gender)
      url += `&species=${species}&status=${status}&gender=${gender}`;
    return axios
      .get(url)
      .then((resp) => {
        dispatch(setCharacters(resp.data.results));
        dispatch(setRequestInfo(resp.data.info));
      })
      .catch((err) => console.error(err));
  };

export const fetchEpisodes = (active, f) => (dispatch) => {
  let url = `https://rickandmortyapi.com/api/episode?page=${active}`;
  if (f) url += `&name=${f}`;
  return axios
    .get(url)
    .then((resp) => {
      dispatch(setEpisodes(resp.data.results));
      dispatch(setRequestInfo(resp.data.info));
    })
    .catch((err) => console.error(err));
};

export const fetchLocations = (active) => (dispatch) => {
  return axios
    .get(`https://rickandmortyapi.com/api/location?page=${active}`)
    .then((resp) => {
      dispatch(setLocations(resp.data.results));
      dispatch(setRequestInfo(resp.data.info));
    })
    .catch((err) => console.error(err));
};

export const saveWatchList = (episod) => {
  if (episod)
    axios
      .get(`https://rickandmortyapi.com/api/episode?name=${episod}`)
      .then((resp) => {
        const el = [];
        el.push({ ...resp.data.results[0], done: false });
        if (localStorage.getItem("watchList")) {
          let newEl = JSON.parse(localStorage.getItem("watchList"));
          if (newEl.find((element) => +element.id === +resp.data.results[0].id))
            alert("elemet in list");
          if (!newEl.find((element) => +element.id === +resp.data.results[0].id)) {
            newEl.push({ ...resp.data.results[0], done: false });
            localStorage.setItem("watchList", JSON.stringify(newEl));
          }
        } else {
          console.log([JSON.stringify(el)]);
          localStorage.setItem("watchList", JSON.stringify(el));
        }
      })
      .catch((err) => console.error(err));
};
